.product {
  display: flex;
  background-color: white;
  margin: 2rem;
  padding: 2rem;
  flex-direction: column;
  width: 210px;
  align-items: center;
  border-radius: 10px;
  box-shadow: -10px 0px 50px rgb(79, 78, 78);
  cursor: pointer;
  transition: all 0.5s;
  &:hover{
    transform: scale(1.02);

  }
  > img {
    object-fit: contain;
    cursor: pointer;
    width: 100%;
    height: 16vh;
  
  }
  > p {
    margin-top: 10px;
  }
  > h3 {
    margin-top: 5px;
  }
  > button {
    padding: 10px 20px;
    position: relative;
    top: 13px;
    border-radius: 3px;
    border: 0;
    outline: 0;
    // background-color: crimson;
    background: #ff004f;
    color: white;
    font-size: 0.9rem;
    cursor: pointer;
    transition: all 0.5s ;
    &:hover{
      background-color: rgb(141, 18, 42);
    }
  }
}
@media only screen and (max-width: 600px) {
  .product{
    width: 75%;
    // height: 45vh;

    > img{
      width: 50%;
    }
  }
}