* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  // font-family: sans-serif;
  font-family: Arial,sans-serif;
  // background-color: #222;
  // background-color: #fef2f2;
}

header {
  background-color: #131921;
  // position: sticky;
  // top: 0;
  // z-index: 10;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  height: 8vh;
  // border: 1px solid rgb(229, 226, 226);
  color: #fff;

  > a{
    cursor: pointer;
    color: #fff;
    text-decoration: none;
    // color: black;
  }
}
nav > a {
  padding: 15px;
  color: #fff;
  cursor: pointer;
  transition: all 0.5s;
  text-decoration: none;
  // color: black;
}
nav a:hover {
  background-color: rgba(220, 20, 60, 0.903);
  border-radius: 13px;
  color: white;
  padding: 10px;
}
